import { ExtensionContext, ViewColumn, WebviewPanel, window } from 'vscode';

// Â≠òÂÇ®urlÂíåWebviewPanelÁöÑÈîÆÂÄºÂØπ
var webViewMap = new Map<string, WebviewPanel>([]);

/**
 * ÂàõÂª∫‰∏Ä‰∏™ÁΩëÈ°µÈù¢Êùø
 * @param viewColumn 
 * @param label Ê†áÈ¢ò
 * @param url ÈìæÊé•
 * @returns WebviewPanel
 */
export function createWebView(viewColumn: ViewColumn, label: string, url: string): WebviewPanel{
    let webViewPanel: WebviewPanel;
    // Âà§Êñ≠ÊòØÂê¶Â∑≤ÁªèÂ≠òÂú®ËØ•ÁΩëÈ°µÈù¢Êùø
    if(!webViewMap.has(url)){
        webViewPanel = window.createWebviewPanel(
            'webView',
            label,
            viewColumn,
            {
                retainContextWhenHidden: true,  // ÊòØÂê¶‰øùÊåÅwebviewÈù¢ÊùøÁöÑÂÜÖÂÆπ,Âç≥‰ΩøÈù¢Êùø‰∏çÂÜçÂèØËßÅ
                enableScripts: true             // htmlÈ°µÊòØÂê¶ÂèØ‰ª•‰ΩøÁî®Scripts
            }
        )
        // Èù¢ÊùøÂµåÂÖ•html
        webViewPanel.webview.html = getIframeHtml(url);
        // Ê∑ªÂä†ËøõwebViewMap
        webViewMap.set(url, webViewPanel);
    }else{
        // Ëé∑ÂèñËØ•webViewPanelÂπ∂ÊòæÁ§∫
        webViewPanel = <WebviewPanel>webViewMap.get(url);
        webViewPanel.reveal();
    }
    // onDidDispose: ÂÖ≥Èó≠ËØ•Èù¢ÊùøÊó∂
    webViewPanel.onDidDispose(() => {
        webViewMap.delete(url);
    });
    return webViewPanel;
}

// Ëøô‰∏™ÊñπÊ≥ïÊ≤°‰ªÄ‰πà‰∫ÜÔºåÂ∞±ÊòØ‰∏Ä‰∏™ ÊúÄÁÆÄÂçïÁöÑÂµåÂÖ• iframe ÁöÑ html È°µÈù¢
function getIframeHtml(url: string): string {
    return `
    <!DOCTYPE html>
    <html lang="en">
        <head>
        <meta charset="utf-8" />
        <meta name="viewport" content="width=device-width, initial-scale=1" />
        <style>
            html,
            body {
                margin: 0 !important;
                padding: 0 !important;
                width: 100%;
                height: 100%;
            }
            .iframeDiv {
                width: 100%;
                height: 100%;
            }
        </style>
        </head>

        <body>
        <iframe id='iframe1' class="iframeDiv" src="${url}" scrolling="auto"></iframe>
        </body>
    </html>
    `;
}